
.res-theaters-area {
    font-size: 0;
}
.res-movies-wrap {
    text-decoration: none;
    font-size: 0;
}
.res-movies-show {
    padding: 10px 20px;
    background: #f8f8f8;
    cursor: pointer;
    font-size: 0;
}
.res-movies-show-child {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
        -ms-flex-align: center;
            align-items: center;
    padding-bottom: 10px;
    border-bottom: 1px solid #d6d6d6;
}
.res-movieMsg {
    -webkit-box-flex: 1;
    -webkit-flex: 1;
        -ms-flex: 1;
            flex: 1;
    padding-left: 20px;
    vertical-align: top;
}
.res-movieMsg h2 {
    font-size: 18px;
    font-weight: 500;
    margin-bottom: 5px;
}
.res-movies-show p {
    font-size: 14px;
    line-height: 2;
    color: #666;
}
.title {
    font-size: 20px;
    font-weight: 700;
    padding-top: 10px;
    padding-bottom: 10px;
    text-align: center;
    background-color: #f2fbfb;
}



